@use '../utils/variables' as *;
form {
  display: flex;
  flex-direction: column;
  gap: 26px;
  max-width: 600px;
  margin: 0 auto;
  height: 100%;
}

input,
textarea {
  padding: 10px 16px;
  border: 1px solid $light-text; 
  border-radius: 4px;
  background-color: $light-text;
  width: 100%;
  transition: border 0.3s ease; 
}
input:focus,
textarea:focus {
  border: 1px solid $brand-color;
  outline: none;
}
textarea {
  resize: none;
  height: 120px;
}

input::placeholder,
textarea::placeholder {
  color: $brand-color;
  opacity: 0.7;   
}

.form-button {
  width: 100%;
}