@use '../../styles/utils/variables' as *;
.form-section {
  padding: 120px 0;
  @media (max-width: $bp-tablet) {
    padding: 60px 0;
  }

  &__container {
    display: flex;
    gap: 48px;
    justify-content: center;
    align-items: center;
    width: 100%;
    @media (max-width: $bp-tablet) {
      flex-direction: column;
      gap: 16px;
    }
  }
  &__title {
    padding-bottom: 26px;
    text-align: center;
  }
  &__content {
    flex: 1;
    width: 100%;
    max-width: 636px;
    
  }
  &__image {
    flex: 1;
    max-width: 500px;
    width: 100%;
    height: auto;
  }
}
